* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #000;
  font-family: montserrat, sans-serif;
  font-size: 1.5rem;
  font-weight: 800;
}

.container {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  flex-flow: column;
  justify-content: space-between;
  gap: 1rem;
  display: flex;
}

#sixDOF-world {
  pointer-events: all;
  touch-action: none;
  background-color: #0ff;
  flex-grow: 0;
  flex-shrink: 0;
  height: 100vw;
  display: block;
  top: 0;
  left: 0;
}

.non-sixDOF-world {
  pointer-events: all;
  background-color: #000;
}

p, span {
  text-align: center;
  color: #fff;
  width: 100%;
}

label {
  cursor: pointer;
  color: #fff;
  background: #000;
  padding: .8rem 2.5rem .8rem .8rem;
  font-size: 2rem;
  font-weight: 300;
  display: inline;
}

.btn-group {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: .5rem;
  width: 100%;
  margin: .25rem;
  display: flex;
}

.btn-group-fixed {
  grid-template-columns: 1fr 1fr;
  width: 100%;
  margin-bottom: 0;
  position: absolute;
  bottom: 0%;
  right: 0%;
}

.menu {
  color: #e1e1ff;
  cursor: pointer;
  background-color: #7d7dff;
  border-width: 2px;
  border-color: #7de1ff;
  border-radius: .7rem;
  padding: 1.5rem;
  font-family: inherit;
  font-size: 2rem;
  font-weight: 600;
  display: block;
}

.btn, .btn:link, .btn:visited {
  color: #e1e1ff;
  cursor: pointer;
  touch-action: manipulation;
  background-color: #7d7dff;
  border-width: 2px;
  border-color: #7de1ff;
  border-radius: .7rem;
  flex: auto;
  padding: .5rem .7rem;
  font-family: inherit;
  font-weight: 600;
  display: block;
}

.btn:disabled, .btn[disabled], .btn.disabled:hover, .btn.disabled:active {
  color: #555;
  background-color: #aaa;
}

.no-uppercase {
  text-transform: none;
}

.slider {
  width: 100%;
}

.span2 {
  text-align: center;
  grid-column: span 2;
  justify-self: center;
}

.span3 {
  text-align: center;
  grid-column: span 3;
  justify-self: center;
}

.span4 {
  text-align: center;
  grid-column: span 4;
  justify-self: center;
}

.special-span {
  text-align: center;
  grid-column: span 3;
  justify-self: center;
}

.grid-item input {
  height: auto;
  margin: 1rem;
}

.center-grid-item {
  place-self: center;
}

.top-margin {
  margin-top: 1rem;
}

.bottom-margin {
  margin-bottom: .5rem;
}

.left-border {
  border-left: 1px solid #fff;
}

.top-border {
  border-top: 1px solid #fff;
}

input[type="radio"], input[type="checkbox"] {
  border: 0;
  width: 2rem;
  height: 2rem;
}

input[type="range"] {
  appearance: none;
  cursor: pointer;
  background: none;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  background-color: #4780fc;
  border-radius: .5rem;
  height: .5rem;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  background-color: #3243ff;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  margin-top: -12px;
}

input[type="range"]:focus::-webkit-slider-thumb {
  outline-offset: .125rem;
  border: 1px solid #053a5f;
  outline: 3px solid #053a5f;
}

.elements-group {
  margin-left: 1rem;
  margin-right: 1rem;
}

.elements {
  touch-action: none;
  flex: 1 0 100%;
  grid-template-columns: 2fr 4fr 2fr;
  align-content: space-around;
  row-gap: 4%;
  padding-top: 4%;
  padding-left: 1%;
  padding-right: 1%;
  display: grid;
}

.radio-elements {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.special-span {
  grid-template-columns: 2fr 5fr 2fr;
}

.numerical {
  text-align: right;
  font-variant-numeric: tabular-nums;
  grid-template-columns: 3fr 2fr 1fr 2fr 1fr 2fr 1fr 2fr;
  font-family: monospace;
}

.grid-item {
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  display: flex;
}

.grid-item-stretch {
  justify-self: stretch;
}

.grid-item-start {
  justify-self: start;
}

.grid-item-end {
  justify-self: end;
}

@media (orientation: landscape) {
  #sixDOF-world {
    width: 100vh;
    height: 100vh;
  }

  .container {
    flex-direction: row;
  }

  .non-sixDOF-world {
    width: calc(100vw - 100vh);
    height: 100%;
  }
}

@media (orientation: portrait) and (width <= 450px) {
  #sixDOF-world {
    align-self: center;
    width: 90vw;
    height: 90vw;
  }

  .non-sixDOF-world {
    width: 100%;
    height: calc(100vh - 90vw);
  }

  .elements {
    padding-top: 5%;
  }
}

@media (orientation: landscape) and (width <= 950px) {
  .special-span {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  }

  #sixDOF-world {
    width: 65vh;
    height: 65vh;
  }

  .non-sixDOF-world {
    width: 100%;
    height: 100%;
  }

  label {
    padding: .4rem .5rem .4rem .4rem;
  }

  .container {
    justify-content: start;
    align-items: start;
  }

  .special-span {
    text-align: center;
    grid-column: span 2;
    justify-self: left;
  }
}

@media (orientation: portrait) and (width >= 451px) {
  #sixDOF-world {
    align-self: center;
    width: 70vw;
    height: 70vw;
  }
}

@media (orientation: landscape) and (width >= 951px) {
  #sixDOF-world {
    width: 80vh;
    height: 80vh;
  }

  .non-sixDOF-world {
    width: 100%;
    height: 100%;
  }
}

